import React, { createContext, useReducer, useEffect } from 'react';
import AppReducer from './AppReducer';



// pretodno direkt dole idevme React.createContext

//initial state 
const initialState = {
   watchlist: localStorage.getItem('watchlist') ? JSON.parse(localStorage.getItem('watchlist')) : [],
   watched: localStorage.getItem('watched') ? JSON.parse(localStorage.getItem('watched')) : [],
}


// create context 
const GlobalContext = createContext(initialState);

//provider components
const AppProvider = ({ children }) => {

   const [state, dispatch] = useReducer(AppReducer, initialState);


   useEffect(() => {
      localStorage.setItem('watchlist', JSON.stringify(state.watchlist))
      localStorage.setItem('watched', JSON.stringify(state.watched))
   }, [state.watchlist, state.watched])


   //actions from reducer

   const addMovieToWatchlist = (movie) => {
      dispatch({ type: 'ADD_MOVIE_TO_WATCHLIST', payload: movie })
   }

   const addMovieToWatched = (movie) => {
      dispatch({ type: 'ADD_MOVIE_TO_WATCHED', payload: movie })
   }


   const removeMovieFromWatchlist = (id) => {
      dispatch({ type: 'REMOVE_MOVIE_FROM_WATCHLIST', payload: id })
   }

   const removeMovieFromWatched = (id) => {
      dispatch({ type: 'REMOVE_MOVIE_FROM_WATCHED', payload: id })
   }


   const disableMovieIfWatched = (movie) => {
      let storedMovie = state.watchlist.find(obj => obj.id === movie.id)
      let storedMovieWatched = state.watched.find(obj => obj.id === movie.id)

      const watchlistDisabled = storedMovie ? true : storedMovieWatched ? true : false;
      return watchlistDisabled;
   }



   return (
      <GlobalContext.Provider
         value={{
            ...state,
            addMovieToWatchlist,
            addMovieToWatched,
            removeMovieFromWatchlist,
            removeMovieFromWatched,
            disableMovieIfWatched
         }}>
         {children}
      </GlobalContext.Provider>
   )

}



export { GlobalContext, AppProvider }